import React, {PureComponent} from 'react';
import Label from 'r-cmui/components/Label';
import Table from 'r-cmui/components/Table';

import Demo1 from './demo1';
import Demo2 from './demo2';

class Page extends PureComponent {
    displayName = 'Page';
    
    render () {
        return (
            <div className='main-container'>
                <h1 className='page-h1'>Transfer </h1>
                <blockquote className='page-tip'>
                    <br/>
                    
                </blockquote>

                <h1 className='page-h1'></h1>

                <Label grid={1} className='code-col'>
                    <Label className='code-box'>
                        <Demo1></Demo1>
                    </Label>
                </Label>

                <Label grid={1} className='code-col'>
                    <Label className='code-box'>
                        <Demo2></Demo2>
                    </Label>
                </Label>

                <h2 className='page-h2'>API</h2>
                <h3 className='page-h3'>Transfer</h3>

                <Table columns={[
                    {name: 'param', text: ''},
                    {name: 'desc', text: ''},
                    {name: 'type', text: ''},
                    {name: 'default', text: ''}
                ]} bordered data={[
                    {param: 'className', desc: 'class', type: 'string', default: ''},
                    {param: 'style', desc: '', type: 'object', default: ''},
                    {param: 'data', desc: '', type: 'array', default: ''},
                    {param: 'transdered', desc: '', type: 'string', default: ''},
                    {param: 'sourceTitle', desc: '', type: 'string', default: 'source'},
                    {param: 'targetTitle', desc: '', type: 'string', default: 'target'},
                    {param: 'onChange', desc: '', type: 'function', default: ''},
                    {param: 'filter', desc: '', type: 'bool', default: 'false'}
                ]}></Table>
            </div>
        );
    }
}

export default Page;



